import { Controller } from '@ali/4ever-cangjie';
import { NewlineGuideData } from '../model/newlineGuide';

export function removeNewlineTriggerText(controller: Controller, needRemoveEmptyParagraph: boolean) {
  return NewlineGuideData.removeTriggerText(controller, needRemoveEmptyParagraph);
}

export function removeNewlineData(controller: Controller) {
  return NewlineGuideData.remove(controller);
}
